import React, {Suspense} from 'react';
import './Section2.scss'
import {Element, animateScroll as scroll, scroller} from 'react-scroll'

import {gallery} from '../Section4/gallery_items'

export default class Section2 extends React.Component {
    render() {
        return (
            <Element name='projects' className="section-two-wrapper d-flex flex-wrap m-0 p-0 col-12" id="projects">
                <div className='section-two-header-wrapper col-12'>
                    <div className='section-two-header-text'>
                        Our finest photoshoots made by us
                    </div>
                    <div className='section-two-header-description'>
                        Choose a specific photo session and browse its content now.
                    </div>
                </div>
                <div className='section-two-gallery-wrapper d-flex flex-wrap justify-content-center col-10'>
                    <Suspense fallback={<p>loading images..</p>}>
                        {gallery.map((data) =>{
                            return(
                                <div className='section-two-gallery-item col-5 col-md-3' style={{backgroundImage: `url('${data.images[3]}')`}} to={data.id} onClick={()=>{scroller.scrollTo(data.id, {duration: 100, delay: 0, smooth: 'easeInOutQuart'})}}>
                                    <div className='section-two-gallery-item-title-wrapper col-12'>
                                        <div className='section-two-gallery-item-title-text col-12'>
                                            {data.name}
                                        </div>
                                    
                                    </div>
                                    <div className='section-two-gallery-item-info-wrapper'>
                                        <div className='section-two-gallery-item-info-bottom d-flex flex-wrap justify-content-between'>
                                            <div className='section-two-gallery-item-info-bottom-date col-12 d-flex justify-content-center'>
                                                {data.date}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })}
                    </Suspense>
                </div>
            </Element>
        )
    }
}