import React, {Suspense} from 'react';
import './Section4.scss'
import {Element, animateScroll as scroll, scroller} from 'react-scroll'
import {gallery} from './gallery_items'
import anime from 'animejs'



export default class Section4 extends React.Component {
    constructor(props) {
        super()
        this.state = {
            image: '',
            open: false
        }

        this.handleGetImageSource = this.handleGetImageSource.bind(this)
        this.handleClose = this.handleClose.bind(this)

        this.fullscreen = React.createRef(null)
        this.wrapper = React.createRef(null)
    }

    componentDidMount(){
        console.log(gallery)
        document.addEventListener('mousedown', this.handleClickOutside);
    }
    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    handleGetImageSource(src){
        console.log(this.state.image)

        this.setState({
            image: src,
            open: true
        })

        setTimeout(()=>{
            anime.timeline({
                easing: 'easeInOutSine',
                targets: this.fullscreen.current,
                duration: 500,
                endDelay: 400,
            })
    
            .add({
                duration: 300,
                opacity: [0, 1]
            })
        }, 0)
    }
    handleClose(){
        

        setTimeout(()=>{
            anime.timeline({
                easing: 'easeInOutSine',
                targets: this.fullscreen.current,
                duration: 500,
                endDelay: 400,
            })
    
            .add({
                duration: 300,
                opacity: [1, 0]
            })
        }, 0)

        setTimeout(() => {
            this.setState({
                open: false
            })
        }, 700)
    }

    handleClickOutside(event) {
        if (this.wrapper && !this.wrapper.current.contains(event.target)) {
            this.setState({
                open: false
            })
        }
    }

  render() {
    return(
        <>
        <div className='section-four-wrapper d-flex flex-wrap justify-content-center m-0 p-0 col-12' id='gallery' ref={this.wrapper}>
            {gallery.map((data, key) => {
                return(
                    <Element className="gallery-section-wrapper d-flex flex-wrap justify-content-center m-0 p-0 col-8" id={data.id} name={data.id}>
                        <div className='section-four-header-wrapper col-12'>
                            <div className='section-four-header'>
                                <div className='section-four-header-title'>
                                    <p>{data.name}</p>
                                </div>
                                <div className='section-four-header-date'>
                                    <p>{data.date}</p>
                                </div>
                            </div>
                        </div>
                        <div className='section-four-gallery-wrapper col-12 d-flex justify-content-center align-items-center flex-wrap'>
                            <div className='section-four-gallery d-flex flex-wrap p-1 col-12'>
                                <Suspense fallback={<p>loading images..</p>}>
                                    <div className='gallery-photo col-6 col-md-3'>
                                        <img className='' src={data.images[0]} alt={key} onClick={() => {this.handleGetImageSource(data.images[0])}}></img>
                                    </div>
                                    <div className='gallery-photo col-6 col-md-3'>
                                        <img className='' src={data.images[1]} alt={key} onClick={() => {this.handleGetImageSource(data.images[1])}}></img>
                                    </div>
                                    <div className='gallery-photo col-6 col-md-6'>
                                        <img className='' src={data.images[2]} alt={key} onClick={() => {this.handleGetImageSource(data.images[2])}}></img>
                                    </div>
                                    <div className='gallery-photo col-6 col-md-12'>
                                        <img className='' src={data.images[3]} alt={key} onClick={() => {this.handleGetImageSource(data.images[3])}}></img>
                                    </div>
                                </Suspense>
                            </div>
                        </div>
                    </Element>

                )
            })}
        {this.state.open ?
            <div className="fullscreen-image col-12" ref={this.fullscreen}>
                <div className='fullscreen-close'>
                    <button onClick={this.handleClose}>
                    <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd"><path fill='#fff' d="M12 11.293l10.293-10.293.707.707-10.293 10.293 10.293 10.293-.707.707-10.293-10.293-10.293 10.293-.707-.707 10.293-10.293-10.293-10.293.707-.707 10.293 10.293z"/></svg>
                    </button>
                </div>
                <img src={this.state.image} alt=''></img>
            </div>
            :
            null
        }
           
        </div>
        
        </>
    )
  }
}



