import React from 'react';
import './Section1.scss'
import {animateScroll as scroll, scroller} from 'react-scroll'

export default class Section1 extends React.Component {
  render() {
    return(
        <div className="container-fluid section-one-wrapper justify-content-center col-12">
          <div className='gradient'></div>
          <div className='section-one-header-wrapper col-12 col-md-4'>
            <div className='section-one-header-text'>
              <p style={{ fontSize: '3rem' }}>kaylalamary</p>
            </div>
            <div className='section-one-description col-12'>
              Welcome to my portfolio, my name is Kayla and if you were looking for glamorous photos of a heavenly body - you've come to the right place.
            </div>
            <div className='section-one-header-buttons col-6 col-md-9'>
              <button className='section-one-button col-12 col-md-5' to='gallery' onClick={()=>{scroller.scrollTo('gallery', {duration: 100, delay: 0, smooth: 'easeInOutQuart'})}}>
                Gallery
              </button>
              <button className='section-one-button-models col-12 col-md-5' to='projects' onClick={()=>{scroller.scrollTo('projects', {duration: 100, delay: 0, smooth: 'easeInOutQuart'})}}>
                Sessions
              </button>
            </div>
          </div>
        </div>
    )
  }
}
