import React from 'react';
// import {Link} from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.min.css';
import './Menu.scss'
import {Link, animateScroll as scroll} from 'react-scroll'

export default class Menu extends React.Component {
    constructor(){
        super()
        this.state={
            scrollPos: 0
        }

        this.handleCheckScrollPos = this.handleCheckScrollPos.bind(this)
    }

    componentDidMount(){
        window.addEventListener('scroll', this.handleCheckScrollPos)
    }
    componentWillUnmount(){
        window.removeEventListener('scroll', this.handleCheckScrollPos)
    }

    handleCheckScrollPos = () =>{
        const winScroll = document.body.scrollTop || document.documentElement.scrollTop
    
        const height = document.documentElement.scrollHeight - document.documentElement.clientHeight
    
        this.setState({ 
            scrollPos: winScroll / height
        })

        console.log(this.state.scrollPos)
    }
  render() {
    return(
        <>
            <div className="container-fluid menu-wrapper col-12">
                <div className="col-3 menu-logo-wrapper d-flex justify-content-center align-items-center">
                    <div className='col-6 menu-logo-text'>
                        kayla
                    </div>
                </div>
                <div className='col-9 menu-links-wrapper'>
                    <div className='menu-links col-12 col-md-5'>
                        <Link className='menu-link' to='projects' smooth={true} duration={100}>Sessions</Link>
                        <Link className='menu-link' to='contact' smooth={true} duration={100}>Contact</Link>
                        <Link className='menu-link' to='gallery' smooth={true} duration={100}>Gallery</Link>
                    </div>
                </div>
            </div>

            {this.state.scrollPos > 0.2 ? 
                <>
                    <div className='scroll-to-top-wrapper' onClick={()=>{scroll.scrollToTop({duration: 100})}}>
                        top
                    </div>
                </>
                :
                null
            }
        </>
    )
  }
}
